import React from 'react';
import ArticleBlock from '../comp/article-block';
import Loading from '../comp/loading';
import PageNav from '../comp/page-nav';
import OverlayScrollbars from 'overlayscrollbars';

type homepageProps = {
  openArticle: Function;
  toPageNumber: Function;
  pageNumber: number;
}

export default class Homepage extends React.Component<homepageProps> {
  state = {
    pageNumber: this.props.pageNumber,
    articleList: [],
    totalPage: 0
  }
  render() {
    let list = null;
    if (this.state.articleList !== []) {
      list = this.state.articleList.map((item: any) => (
        <ArticleBlock key={item.fileName} title={item.title} articleName={item.fileName} description={item.description} onClick={this.openArticle} />
      ))
    }
    return (
      <div className="homepage">
        <Loading show={this.state.articleList.length === 0? true : false} />
        {list}
        <PageNav currentPage={this.state.pageNumber} toPageNumber={this.props.toPageNumber} totalPage={this.state.totalPage} url={"/"} />
      </div>
    )
  }
  openArticle = (name: string) => {
    this.props.openArticle(name);
  }
  updateContent = () => {
    if (this.state.articleList.length === 0 && this.state.pageNumber !== undefined) {
      fetch("/source/homepage/" + this.state.pageNumber + ".json")
        .then((res) => {
          return res.json();
        })
        .then((json) => {
          this.setState({articleList: json}, ()=>{OverlayScrollbars(document.querySelectorAll("pre"), { scrollbars: { autoHide: 'scroll' } });})
        })

      fetch("/source/homepage/homepage.json")
        .then((res) => {
          return res.json();
        })
        .then((json) => {
          this.setState({totalPage: json.page})
        })
    }
  }
  componentDidMount = () => {
    this.updateContent();
  }
  componentDidUpdate = () => {
    this.updateContent();
  }
  static getDerivedStateFromProps(props: homepageProps, state: any) {
    const {pageNumber} = props;
    if (pageNumber !== state.pageNumber) {
      return {pageNumber: pageNumber, articleList: []};
    }
    return null;
  }
}