/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';

const roundButtonStyle = css`
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center; 
  align-items: center;
  height: 40px;
  width: 40px;
  background: none;
  transition: 200ms;
  border: none;
  outline: none;
  user-select: none;
  &::after {
    position: absolute;
    z-index: -1;
    content: "";
    width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    background: #000;
    transform: translate(-50%, -50%) scale(0);
  }
  &:focus {
    border: 2px solid #000;
  }
  &:hover {
    color: #FFF;
    border: none !important;
    span {
      background: #FFF;
    }
    &::after {
      transition: 300ms;
      transform: translate(-50%, -50%) scale(1);
    }
  }
  &:active {
    color: #FFF;
    border: none !important;
    transform: scale(0.9);
    span {
      background: #FFF;
    }
    &::after {
      transform: translate(-50%, -50%) scale(1);
    }
  }
`

type roundButtonProps = {
  className?: string;
  onClick?: Function;
}

export default class RoundButton extends React.Component<roundButtonProps> {
  private btnRef = React.createRef<any>();
  render() {
    return(
      <button className={this.props.className} ref={this.btnRef} css={roundButtonStyle} onClick={this.onClick}>
        {this.props.children}
      </button>
    )
  }
  onClick = (event: any) => {
    if (this.props.onClick !== undefined) this.props.onClick();
    if (event.detail && document.activeElement !== null) {
      this.btnRef.current.blur();
    }
  }
}