/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import React from 'react';

interface navProps {
  toPageNumber: Function;
  currentPage: number;
  totalPage: number;
  show?: boolean;
  url: string;
}

export default class PageNav extends React.Component<navProps> {
  render() {
    let prev, next;
    if (this.props.currentPage - 1 >= 1) {
      prev = true;
    }
    if (this.props.currentPage + 1 <= this.props.totalPage) {
      next = true;
    }

    let num = [0,0,0,0,0];
    if (this.props.currentPage - 2 >= 1) {
      num[0] = this.props.currentPage - 2;
    } else if (this.props.currentPage - 1 >= 1) { 
      num[0] = this.props.currentPage - 1;
    } else {
      num[0] = this.props.currentPage;
    }

    for (let i=1; i<5; i++) {
      if (num[i-1] + 1 <= this.props.totalPage) {
        num[i] = num[i-1] + 1;
      } else {
        break;
      }
    }

    return(
      <div css={css`display: ${this.props.totalPage === 0? "none" : "block"}; width: 100%; text-align: center;`}>
        <div css={css`display: inline-block;`}>
          <PageNumber num={"<"} onClick={this.toPreviousPage} url={prev? this.props.url + "page/" + (this.props.currentPage - 1) : ""} />
          <PageNumber num={num[0].toString()} onClick={()=>{this.toPageNumber(num[0])}} url={this.props.url + "page/" + num[0]} selected={this.props.currentPage === num[0]? true : false} />
          <PageNumber num={num[1].toString()} onClick={()=>{this.toPageNumber(num[1])}} url={this.props.url + "page/" + num[1]} selected={this.props.currentPage === num[1]? true : false} />
          <PageNumber num={num[2].toString()} onClick={()=>{this.toPageNumber(num[2])}} url={this.props.url + "page/" + num[2]} selected={this.props.currentPage === num[2]? true : false}/>
          <PageNumber num={num[3].toString()} onClick={()=>{this.toPageNumber(num[3])}} url={this.props.url + "page/" + num[3]} selected={this.props.currentPage === num[3]? true : false} />
          <PageNumber num={num[4].toString()} onClick={()=>{this.toPageNumber(num[4])}} url={this.props.url + "page/" + num[4]} selected={this.props.currentPage === num[4]? true : false} />
          <PageNumber num={">"} onClick={this.toNextPage} url={next? this.props.url + "page/" + (this.props.currentPage + 1) : ""} />
        </div>
      </div>
    )
  }
  toPageNumber = (page: number) => {
    if (page !== 0) {
      this.props.toPageNumber(page);
    }
  }
  toPreviousPage = () => {
    if (this.props.currentPage - 1 >= 1) {
      this.props.toPageNumber(this.props.currentPage - 1);
    }
  }
  toNextPage = () => {
    if (this.props.currentPage + 1 <= this.props.totalPage) {
      this.props.toPageNumber(this.props.currentPage + 1);
    }
  }
}

interface numProps {
  num: string;
  selected?: boolean;
  onClick?: Function;
  url?: string;
}

class PageNumber extends React.Component<numProps> {
  render() {
    const numberStyle = css`
      display: ${this.props.num === "0"? "none" : "inline-block"};
      padding: 0 10px;
      text-align: center;
      color: ${this.props.selected? "#000" : "#FFF"};
      -webkit-text-stroke: ${this.props.selected? "0px" : "2px"} #000;  
      -webkit-text-fill-color : ${this.props.selected? "#000" : "transparent"};
      -webkit-tap-highlight-color: transparent;
      font-weight: bold;
      font-size: 50px;
      text-decoration: none;
      user-select: none;
      &:nth-of-type(odd) {
        transform: translateY(5px);
      }
      &:nth-of-type(even) {
        transform: translateY(-5px);
      }
      @media (any-hover: hover) {
        &:hover {
          -webkit-text-stroke: 0px;  
          -webkit-text-fill-color : #000;
        }
      }
      &:active {
        -webkit-text-stroke: 0px;  
        -webkit-text-fill-color : #000;
      }
    `
    return(
      <a href={this.props.url} css={numberStyle} onClick={this.onClick}>{this.props.num}</a>
    )
  }
  onClick = (event: any) => {
    event.preventDefault();
    if (this.props.onClick !== undefined) {
      this.props.onClick();
    }
  }
}