/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import ReactMarkdown from 'react-markdown';
import OverlayScrollbars from 'overlayscrollbars';
import Helmet from 'react-helmet';
import Loading from '../comp/loading';
import articleStyle from '../style/articleStyle';

interface articleProps {
  articleName: string;
  hover?: boolean;
}

export default class Article extends React.Component<articleProps> {
  state = {
    articleName: this.props.articleName,
    content: "",
    head: ""
  }
  render() {
    const titleContainerStyle = css`
      display: flex;
      flex-direction: column-reverse;
      z-index: 1;
      min-height: ${getInfo(this.state.head, "picture")? "290px" : "auto"};
      /* background: red; */
    `

    const titleStyle = css`
      position: relative;
      margin: 0 0 20px 0;
      width: fit-content;
      color: #FFF;
      &::before {
        content: "";
        z-index: -1;
        position: absolute;
        left: -40px;
        top: -3px;
        width: calc(100% + 40px);
        height: 100%;
        background: #000;
      }
    `

    const innerStyle = css`
      padding: ${this.props.hover? "50px 40px 10px 40px" : "20px 20px 10px 20px"};
      transition: 300ms;
      opacity: ${this.state.content === "" ? 0 : 1};
      /* @media screen and (max-width: 800px) {
        padding: ${this.props.hover? "50px 20px 10px 20px" : "20px 20px 10px 20px"};
      } */
    `

    const coverStyle = css`
      display: ${getInfo(this.state.head, "picture")? "block" : "none"};
      z-index: -1;
      position: absolute;
      border-bottom-style: solid;
      border-left-style: ${this.props.hover? "solid" : "none"};
      border-right-style: ${this.props.hover? "solid" : "none"};
      border-color: #000;
      border-width: ${this.props.hover? "5px" : "3px"};
      box-sizing: border-box;
      left: 0;
      top: 0;
      width: 100%;
      height: 300px;
      background: url(${getInfo(this.state.head, "picture")}) center/cover;
    `

    return (
      <div css={articleStyle}>
        <Helmet>
          <title>{getInfo(this.state.head, "title") + " - Nanoscale"}</title>
        </Helmet>
        <Loading show={this.state.content === "" ? true : false} />
        <div css={innerStyle}>
          <div css={coverStyle}></div>
          <div css={titleContainerStyle}>
            <h1 className="title" css={titleStyle}>{getInfo(this.state.head, "title")}</h1>
          </div>
          <ReactMarkdown source={this.state.content} escapeHtml={false} />
        </div>
      </div>
    )
  }
  updateContent = () => {
    if (this.state.articleName !== "" && this.state.content === "") {
      fetch("/source/article/" + this.state.articleName + ".md")
        .then((res) => res.text())
        .then(data => {
          let head = getHead(data);
          let content = getContent(data);
          this.setState({ content: content, head: head }, ()=>{
            OverlayScrollbars(document.querySelectorAll("pre"), { scrollbars: { autoHide: 'scroll' } });
          });
        })
    }
  }
  componentDidMount() {
    this.updateContent();
  }
  // static getDerivedStateFromProps(props: articlePageProps, state: any) {
  //     const {articleName} = props;
  //     if (articleName !== state.articleName) {
  //         return {articleName, content: "", head: ""};
  //     }
  //     return null;
  // }
}

function getHead(text: string) {
  const headRe = /<!--[\s\S]*?-->/;
  const ex = headRe.exec(text);
  if (ex !== null) {
    return ex[0];
  } else {
    return "";
  }
}

function getContent(text: string) {
  const headRe = /<!--[\s\S]*?-->/;
  const content = text.replace(headRe, "");
  return content;
}

function getInfo(text: string, label: string) {
  let re, rp;
  switch (label) {
    case "title":
      re = /title:\s*.*/;
      rp = /title:\s*/;
      break;
    case "createTime":
      re = /createtime:\s*.*/;
      rp = /createtime:\s*/;
      break;
    case "picture":
      re = /picture:\s*.*/;
      rp = /picture:\s*/;
      break;
    default:
      return "";
  }

  const ex = re.exec(text);
  if (ex !== null) {
    let info = ex[0];
    info = info.replace(rp, "");
    return info;
  } else {
    return "";
  }
}