import React from 'react';
import Article from '../comp/article';
import Block from '../comp/block';

interface props {
  articleName: string;
}

export default class ArticlePage extends React.Component<props> {
  render() {
    return(
      <Block>
        <div>
          <Article articleName={this.props.articleName} />
        </div>
      </Block>
    )
  }
}