/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
const UA = window.navigator.userAgent;
const isFF = UA.indexOf("Firefox") !== -1;

type leftMenuProps = {
    left: number,
    transition: boolean
}

export default class LeftMenu extends React.Component<leftMenuProps> {
    state = {
        left: this.props.left,
        transition: this.props.transition
    }
    render() {
        const leftMenuStyle = css`
            position: fixed;
            z-index: 997;
            top: 0;
            left: ${this.state.left + "px"};
            width: 300px;
            height: calc(100% + 50px);
            background: ${isFF? "#FFF" : "rgba(255,255,255,0.7)"};
            backdrop-filter: blur(15px) saturate(180%);
            border-right: 1px solid rgba(0,0,0,0.05);
            box-sizing: border-box;
            transition: width 300ms ${this.state.transition? ", left 350ms" : ""};
            @media screen and (max-width: 500px) {
                width: 100%;
                left: ${this.state.left};
            }
        `
        return(
            <div css={leftMenuStyle}>

            </div>
        )
    }
    static getDerivedStateFromProps(props: leftMenuProps, state: any) {
        const {left, transition} = props;
        if (left !== state.left) {
            return {left};
        }
        if (transition !== state.transition) {
            return {transition};
        }
        return null;
    }
}