import { css } from '@emotion/core';
import color from './color';

const articleStyle = css`
  position: relative;
  z-index: 1;
  font-size: 18px;
  img {
    max-width: 100%;
  }
  pre {
    background: ${color.lightGrey};
    padding: 15px;
    font-size: 0.8em;
    overflow: auto;
  }
  code {
    font-family: 'Courier Prime', 'Source Han Sans Old Style', monospace;
  }
  blockquote {
    position: relative;
    width: calc(100% + 40px);
    left: -20px;
    background: #000;
    margin: 30px 0;
    padding: 5px 40px;
    box-sizing: border-box;
    color: #FFF;
    &::before {
      content: "\\e634";
      font-family: 'iconfont';
      position: absolute;
      font-size: 40px;
      top: -25px;
      left: 10px;
      text-shadow:
        -1px -1px 0 #000,  
        1px -1px 0 #000,
        -1px  1px 0 #000,
        2px  2px 0 #000;
    }
    &::after {
      content: "\\e635";
      font-family: 'iconfont';
      position: absolute;
      font-size: 40px;
      bottom: -25px;
      right: 10px;
      text-shadow:
        -1px -1px 0 #000,  
        1px -1px 0 #000,
        -1px  1px 0 #000,
        2px  2px 0 #000;
    }
    a {
      color: #FFF;
      text-shadow: .03em 0 #000, -.03em 0 #000, 0 .03em #000, 0 -.03em #000, .06em 0 #000, -.06em 0 #000, .09em 0 #000, -.09em 0 #000, .12em 0 #000, -.12em 0 #000, .15em 0 #000, -.15em 0 #000;
      background-image: linear-gradient(#000, #000), linear-gradient(#000, #000), linear-gradient(#FFF, #FFF);
    }
  }
  a {
    color: #000;
    text-decoration: none;
    text-shadow: .03em 0 #FFF, -.03em 0 #FFF, 0 .03em #FFF, 0 -.03em #FFF, .06em 0 #FFF, -.06em 0 #FFF, .09em 0 #FFF, -.09em 0 #FFF, .12em 0 #FFF, -.12em 0 #FFF, .15em 0 #FFF, -.15em 0 #FFF;
    background-image: linear-gradient(#FFF, #FFF), linear-gradient(#FFF, #FFF), linear-gradient(#000, #000);
    background-size: .05em 1px, .05em 1px, 1px 1px;
    background-repeat: no-repeat, no-repeat, repeat-x;
    background-position: 0% 90%, 100% 90%, 0% 90%;
    &::selection {
      text-shadow: none;
      /* background-image: linear-gradient(#000, #000), linear-gradient(#000, #000), linear-gradient(#FFF, #FFF); */
    }
    &:active {
      color: ${color.highlight};
      background-image: linear-gradient(#FFF, #FFF), linear-gradient(#FFF, #FFF), linear-gradient(${color.highlight}, ${color.highlight});
    }
  }
  h2 {
    display: inline-block;
    position: relative;
    margin: 0;
    &::before {
      position: absolute;
      content: "";
      width: calc(100% + 30px);
      height: 3px;
      left: -20px;
      bottom: 0px;
      background: #000;
    }
  }
`

export default articleStyle;