/** @jsx jsx */
import { jsx, css, keyframes } from '@emotion/core';
import React from 'react';
import color from '../style/color';

type props = {
    show?: boolean;
}

export default class Loading extends React.Component<props> {
    render() {
        const outerAnimation = keyframes`
            from {
                transform: rotate(0)
            }
            40% {
                transform: rotate(0)
            }
            50% {
                transform: rotate(-180deg)
            }
            90% {
                transform: rotate(-180deg)
            }
            100% {
                transform: rotate(-360deg)
            }
        `

        const innerAnimation = keyframes`
            from {
                height: 40px
            }
            40% {
                height: 0px
            }
            50% {
                height: 40px
            }
            90% {
                height: 0px
            }
            100% {
                height: 40px
            }
        `

        const loadingStyle = css`
            position: absolute;
            display: ${!this.props.show? "none" : "block"};
            left: calc(50% - 25px);
            top: calc(50% - 25px);
            width: 50px;
            height: 50px;
            background: #000;
            animation: ${outerAnimation} 4s ease infinite;
            &::after {
                content: "";
                position: absolute;
                left: 5px;
                top: 5px;
                height: 40px;
                width: 40px;
                background: ${color.highlight};
                animation: ${innerAnimation} 4s ease infinite;
            }
        `

        return(
            <div css={loadingStyle}></div>
        )
    }
}