/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import LeftMenu from './left-menu';
import TopBar from './top-bar';

interface headerProps {
    hideTopBar?: boolean;
    page: string;
    toPage: Function;
}

export default class Header extends React.Component<headerProps> {
    state = {
        leftMenuLeft: 0,
        width: document.body.clientWidth <= 500? document.body.clientWidth : 300,
        transition: true
    }
    render() {
        const swipeAreaStyle = css`
            position: fixed;
            z-index: 998;
            height: 100%;
            top: 0;
            left: ${this.state.leftMenuLeft === 0? this.state.width - 10 + "px" : "0"};
            width: 10px;
        `

        return(
            <div>
                <TopBar toggleLeftMenu={this.toggleLeftMenu} leftMenuLeft={this.state.leftMenuLeft} hide={this.props.hideTopBar} page={this.props.page} toPage={this.props.toPage} />
                <LeftMenu left={this.state.leftMenuLeft} transition={this.state.transition} />
                <div css={swipeAreaStyle} onTouchStart={this.touchStart}></div>
            </div>
        )
    }
    componentDidMount = () => {
        this.onResize();
        window.onresize = this.onResize;
    }
    onResize = () => {
        const width = document.body.clientWidth <= 500? document.body.clientWidth : 300;
        this.setState({leftMenuLeft: -width, width: width});
    }
    toggleLeftMenu = () => {
        if (this.state.leftMenuLeft === -this.state.width) {
            this.setState({leftMenuLeft: 0})
        } else {
            this.setState({leftMenuLeft: -this.state.width})
        }
    }
    touchStart = () =>{
        if (this.state.transition) this.setState({transition: false});
        document.addEventListener('touchmove', this.touchMove);
        document.addEventListener('touchend', this.touchEnd);
    }
    touchMove = (event: any) => {
        let left: number;
        left = (event.changedTouches[0].clientX - this.state.width);
        if (event.changedTouches[0].clientX - this.state.width > 0) left = 0;
        if (this.state.leftMenuLeft !== left) {
            this.setState({leftMenuLeft: left});
            this.forceUpdate();
        }
        // console.log(event.changedTouches[0].clientX, event.changedTouches[0].clientY);
    }
    touchEnd = (event: any) => {
        if (!this.state.transition) this.setState({transition: true});
        if (event.changedTouches[0].clientX < this.state.width / 2) this.setState({leftMenuLeft: -this.state.width});
        if (event.changedTouches[0].clientX >= this.state.width / 2) this.setState({leftMenuLeft: 0});
        document.removeEventListener('touchmove', this.touchMove);
        document.removeEventListener('touchend', this.touchEnd);
    }
}