/** @jsx jsx */
import { jsx, css, Global } from '@emotion/core'
import React from 'react';
import { createBrowserHistory } from 'history';
import Helmet from 'react-helmet';
import OverlayScrollbars from 'overlayscrollbars';
import Homepage from '../page/homepage';
import Footer from './footer';
import Header from './header/header';
import HoverPage from './hover-page';
import ArticlePage from '../page/article-page';

const history = createBrowserHistory();
let initialPage = "";
let initialPageData = {
  pageNumber: 0,
  articleName: ""
};
router();

export default class Frame extends React.Component {
  state = {
    hoverPage: false,
    page: initialPage,
    pageNumber: initialPageData.pageNumber,
    articleName: initialPageData.articleName,
  }
  render() {
    const contentOuterStyle = css`
      margin: 0 auto;
      max-width: 1300px;
      filter: ${this.state.hoverPage ? "blur(10px)" : "none"};
      transition: filter 300ms;
      pointer-events: ${this.state.hoverPage ? "none" : "auto"};
    `
    const contentInnerStyle = css`
      margin-top: 70px;
      width: 100%;
    `
    return (
      <div lang="zh-TW">
        <Global
          styles={css`
            body {
              overflow: ${this.state.hoverPage ? "hidden" : "auto"};
            }
          `}
        />
        <Helmet>
          <title>Nanoscale</title>
        </Helmet>
        <Header hideTopBar={this.state.hoverPage} page={this.state.page} toPage={this.toPage} />
        <div css={contentOuterStyle}>
          <div css={contentInnerStyle}>
            <Switch page={this.state.page} pageNumber={this.state.pageNumber} articleName={this.state.articleName} openArticle={this.openArticle} toPageNumber={this.toPageNumber} />
          </div>
          <Footer />
        </div>
        <HoverPage show={this.state.hoverPage} hideHoverPage={this.hideHoverPage} history={history} articleName={this.state.articleName} />
      </div>
    )
  }
  componentDidMount = () => {
    window.addEventListener('popstate', this.urlChanged);
    OverlayScrollbars(document.body, { scrollbars: { autoHide: 'scroll' } });
  }
  urlChanged = () => {
    router();
    if (initialPage !== this.state.page ||
      initialPageData.pageNumber !== this.state.pageNumber ||
      initialPageData.articleName !== this.state.articleName) {
      this.setState({ page: initialPage, pageNumber: initialPageData.pageNumber, articleName: initialPageData.articleName });
    }
  }
  showHoverPage = () => {
    window.removeEventListener('popstate', this.urlChanged);
    this.setState({ hoverPage: true });
  }
  hideHoverPage = () => {
    window.addEventListener('popstate', this.urlChanged);
    this.setState({ hoverPage: false });
  }
  openArticle = (name: string) => {
    history.push(`/article/${name}/`);
    this.setState({ articleName: name, hoverPage: true })
  }
  toPage = (page: string, pageNumber: number, articleName: string) => {
    this.setState({ page });
    if (pageNumber) this.setState({ pageNumber });
    if (articleName) this.setState({ articleName });
    switch (page) {
      default:
      case "homepage":
        history.push('/');
        break;
    }
  }
  toPageNumber = (page: number) => {
    this.setState({pageNumber: page});
    history.push(`/page/${page}`);
  }
}

type switchProps = {
  page: string;
  openArticle: Function;
  toPageNumber: Function;
  pageNumber: number;
  articleName: string;
}

class Switch extends React.Component<switchProps> {
  render() {
    let page;
    switch (this.props.page) {
      default:
      case "homepage":
        page = <Homepage openArticle={this.props.openArticle} toPageNumber={this.props.toPageNumber} pageNumber={this.props.pageNumber} />;
        break;
      case "article":
        page = (
          <ArticlePage articleName={this.props.articleName} />
        )
        break;
    }
    return page;
  }
}

function router() {
  // console.log(window.location.hostname, window.location.href);
  if (window.location.href.indexOf("/?jump=") !== -1) {
    // console.log("jump");
    let a = window.location.href.split("/?jump=");
    history.push(a[1] + "?");
  }
  if (window.location.pathname.startsWith("/article")) {
    initialPage = "article";
    let a = window.location.pathname.split("/");
    initialPageData.articleName = a[2];
  } else if (window.location.pathname.startsWith("/page")) {
    initialPage = "homepage";
    let a = window.location.pathname.split("/");
    initialPageData.pageNumber = parseInt(a[2]);
  } else {
    initialPage = "homepage";
    initialPageData.pageNumber = 1;
  }
}