/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import RoundButton from '../round-btn';
const UA = window.navigator.userAgent;
const isFF = UA.indexOf("Firefox") !== -1;

interface topBarProps {
  toggleLeftMenu: Function;
  leftMenuLeft: number;
  hide?: boolean;
  page: string;
  toPage: Function;
}

export default class TopBar extends React.Component<topBarProps> {
  render() {
    const topBarContainerStyle = css`
      display: ${this.props.hide ? "none" : "block"};
      position: fixed;
      z-index: 998;
      top: 0;
      width: 100%;
      height: 40px;
      background: ${isFF ? "#FFF" : "rgba(255,255,255,0.8)"};
      backdrop-filter: blur(10px) saturate(180%);
      border-bottom: 1px solid rgba(0,0,0,0.05);
      white-space: nowrap;
    `
    const topBarInnerStyle = css`
      display: flex;
      max-width: 1300px;
      margin: 0 auto;
    `
    const topBarItemContainerStyle = css`
      display: flex;
      width: calc(100% - 40px);
      overflow-x: auto;
      overflow-y: hidden;
      &::-webkit-scrollbar {
        display: none;
      }
      @media screen and (max-width: 1400px) {
        margin-left: 40px;
        mask-image: -webkit-linear-gradient(right, transparent, black 20px);
      }
    `

    return (
      <div>
        <MenuButton onClick={this.props.toggleLeftMenu} leftMenuLeft={this.props.leftMenuLeft} />
        <div css={topBarContainerStyle}>
          <div css={topBarInnerStyle}>
            <div css={topBarItemContainerStyle}>
              <Item href={"/"} title="home" onClick={() => { this.navOnClick("homepage", 1) }} selected={this.props.page === "homepage" ? true : false} />
              <Item title="categories" />
              <Item title="archive" />
              <Item title="tags" />
              <Item title="about" />
            </div>
          </div>
        </div>
      </div>
    )
  }
  menuOnClick = () => {
    this.props.toggleLeftMenu();
  }
  navOnClick = (page: string, pageNumber?: number, articleName?: string) => {
    this.props.toPage(page, pageNumber, articleName);
  }
}

type itemProps = {
  title: string;
  href?: string;
  selected?: boolean;
  onClick?: Function;
}

const itemStyle = css`
  position: relative;
  height: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  font-family: 'Secular One', 'Source Han Sans Old Style', sans-serif;
  padding: 0 15px;
  cursor: pointer;
  transition: 200ms;
  box-sizing: border-box;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  @media (any-hover: hover) {
    &:hover {
      background: rgba(0,0,0,0.1);
    }
  }
  &:active {
    background: rgba(0,0,0,0.2);
  }
  &.selected {
    border-bottom: 2px solid #000;
  }
`

class Item extends React.Component<itemProps> {
  render() {
    return (
      <a css={itemStyle} href={this.props.href} className={"top-bar-item" + (this.props.selected ? " selected" : "")} onClick={this.onClick}>
        {this.props.title}
      </a>
    )
  }
  onClick = (event: any) => {
    event.preventDefault();
    if (this.props.onClick !== undefined) this.props.onClick();
  }
}

type menuButtonProps = {
  onClick: Function;
  leftMenuLeft: number;
}

const menuButtonStyle = css`
    position: fixed;
    z-index: 999;
    top: 0;
`

class MenuButton extends React.Component<menuButtonProps> {
  render() {
    const spanStyle = `height: 2px; width: 20px; background: #000; margin: 2px 2px; transition: 200ms; `;
    const width = document.body.clientWidth <= 500 ? document.body.clientWidth : 300;
    const progress = 1 - Math.abs(this.props.leftMenuLeft) / width;
    // console.log(progress);
    return (
      <div css={menuButtonStyle}>
        <RoundButton onClick={this.onClick}>
          <div css={css`display: flex; flex-direction: column;`}>
            <span css={css`${spanStyle} transform: rotate(${-45 * progress}deg) scale(${1 - 0.52 * progress}, 0.9) translate(${-14 * progress}px, ${-2.5 * progress}px); `}></span>
            <span css={css`${spanStyle} transform: scale(${1 - 0.1 * progress}, 0.9)`}></span>
            <span css={css`${spanStyle} transform: rotate(${45 * progress}deg) scale(${1 - 0.52 * progress}, 0.9) translate(${-14 * progress}px, ${2.5 * progress}px); `}></span>
          </div>
        </RoundButton>
      </div>
    )
  }
  onClick = () => {
    this.props.onClick();
  }
}