/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import React from 'react';
import ReactMarkdown from 'react-markdown';
import Block from './block';
import articleStyle from '../style/articleStyle';

type articleBlockProps = {
  title?: string;
  onClick: Function;
  articleName: string;
  description: string;
};

export default class ArticleBlock extends React.Component<articleBlockProps> {
  render() {
    return(
      <Block>
        <div css={css`margin: 20px;`}>
          <Title title={this.props.title} onClick={this.onClick} articleName={this.props.articleName} />
          <ReactMarkdown source={this.props.description} escapeHtml={false} css={articleStyle}/>
        </div>
      </Block>
    )
  }
  onClick = () => {
    this.props.onClick(this.props.articleName);
  }
};

type titleProps = {
    title?: string;
    onClick?: Function;
    articleName: string;
};

const titleStyle = css`
    display: inline-block;
    z-index: 1;
    position: relative;
    text-decoration: none;
    color: #000;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: 200ms;
    &::after {
        content: "";
        z-index: -1;
        position: absolute;
        top: -4px;
        left: -30px;
        width: 0;
        height: 100%;
        transition: width 300ms, background 200ms;
        background: #000;
    }
    @media (any-hover: hover) {
        &:hover {
            color: #FFF;
            &::after {
                width: calc(100% + 50px);
            }
        }
    }
    &:active {
        color: #FFF;
        &::after {
            width: calc(100% + 50px);
        }
    }
    h1 {
        margin: 0 0 10px 0px;
        font-size: 1.7em;
    }
`

class Title extends React.Component<titleProps> {
    render() {
        return(
            <a href={`/article/${this.props.articleName}/`} css={titleStyle} onClick={this.onClick}>
                <h1>{this.props.title}</h1>
            </a>
        )
    }
    onClick = (event: any) => {
        event.preventDefault();
        if (this.props.onClick !== undefined) this.props.onClick();
    }
};