/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import React from 'react';
import color from '../style/color';

const footerStyle = css`
    max-width: 1300px;
    margin: 0 auto;
    padding: 15px;
    color: ${color.grey};
`

export default class Footer extends React.Component {
    render() {
        return(
            <div css={footerStyle}>
                © 2020 Nanoscale
            </div>
        )
    }
}